.wrapper {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.projectItem {
	filter: drop-shadow(2px 2px 2px var(--shadow));
	object-fit: contain;
	width: 100%;
	height: 100%;
}

.projectItem:hover {
	filter: drop-shadow(16px 16px 16px var(--shadow));
}